.app#about {
  background-image: url("../imgs/Shaffer_Taylor-17.Lite.jpg");
  background-position: center center;
}
.about-container {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
}
.about-content {
  height: fit-content;
  width: 600px;
  background-color: rgba(241, 241, 241, 0.8);
  margin: 100px 50px;
  border-radius: 3px;
}
.about-content h1 {
  font-family: "Unbound", Georgia, "Times New Roman", Times, serif;
  font-size: 64px;
  padding: 10px;
}
.about-content p {
  padding: 0 10px 15px 10px;
  margin: 0 25px;
}

@media (max-width: 1100px) {
  .app#about {
    background-position-x: 35%;
  }
  .about-content {
    padding: 0;
    margin: 60vh 5vw 25px 5vw;
  }
  .about-content > h1 {
    font-size: 30px;
    padding: 3px 0;
  }
  .about-content > p {
    font-size: 12px;
    padding: 2px 5px;
    margin: 0;
  }
}
